
.siteLoading-open{overflow:hidden
}
.siteLoading{position:fixed;left:0;top:0;width:100%;height:100%;background:#1f1f23;z-index:1
}
.siteLoading.hide{opacity:0;filter:alpha(opacity=0);visibility:hidden;transition:all 300ms cubic-bezier(0.165, 0.84, 0.44, 1)
}
.siteLoading .loadingLines .line1,.siteLoading .loadingLines .line2{background:#ffffff
}



