
@font-face{font-family:'Cormorant Infant';font-style:normal;font-weight:400;src:url(./assets/font/Cormorant-Infant.eot);src:local("Cormorant Infant"),local("CormorantInfant-Regular"),url(./assets/font/Cormorant-Infant.eot?#iefix) format("embedded-opentype"),url(./assets/font/Cormorant-Infant.svg#CormorantInfant) format("svg")
}
@font-face{font-family:'Cormorant Infant';font-style:italic;font-weight:300;src:local("Cormorant Infant Light Italic"),local("CormorantInfant-LightItalic"),url(./assets/font/Cormorant-Infant-Light-Italic.woff) format("woff"),url(./assets/font/Cormorant-Infant-Light-Italic.ttf) format("truetype"),url(./assets/font/Cormorant-Infant-Light-Italic.woff2) format("woff2")
}
@font-face{font-family:'Cormorant Infant';font-style:italic;font-weight:400;src:local("Cormorant Infant Italic"),local("CormorantInfant-Italic"),url(./assets/font/Cormorant-Infant-Italic.woff) format("woff"),url(./assets/font/Cormorant-Infant-Italic.ttf) format("truetype"),url(./assets/font/Cormorant-Infant-Italic.woff2) format("woff2")
}
*{-webkit-tap-highlight-color:transparent
}
html,body{height:100%;background:#fff
}
html{-webkit-overflow-scrolling:touch;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;height:100%;overflow-y:scroll
}
body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", SimSun, sans-serif;line-height:1.6;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:100%;font-style:italic;margin:0
}
h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal;margin:0
}
a{text-decoration:none;color:#656575
}
a:hover{color:#16161f
}
.card{box-shadow:0 2px 7px rgba(0,0,0,0.2)
}
.align-center{text-align:center
}
#app{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale
}
.siteHeader{width:995px;margin:0 auto;*zoom:1;padding:100px 0
}
.siteHeader:after{content:".";display:block;clear:both;height:0;visibility:hidden;overflow:hidden
}
.siteHeader h2{font-family:'Cormorant Infant', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", serif;font-size:21px;float:left;height:100px;line-height:100px;padding-left:147.5px
}
.siteHeader h2 a{color:#16161f
}
.siteHeader .line{background:url(./assets/line.png) no-repeat;background-size:100px 100px;float:left;width:100px;height:100px;line-height:100px
}
.siteHeader .rightBlock{float:left;padding-top:25px;font-family:'Cormorant Infant', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", serif
}
.siteHeader .rightBlock h1{text-align:center
}
.siteHeader .rightBlock h1 a{color:#16161f
}
.siteHeader .rightBlock .nav a.noLink{text-decoration:line-through
}
.siteHeader .rightBlock .nav span{padding:0 4px
}
@keyframes piston{
0%{transform:rotate(22deg) translate3d(0, -25px, 0)
}
50%{transform:rotate(22deg) translate3d(0, 25px, 0)
}
100%{transform:rotate(22deg) translate3d(0, -25px, 0)
}
}
@keyframes piston_rev{
0%{transform:rotate(22deg) translate3d(0, 25px, 0)
}
50%{transform:rotate(22deg) translate3d(0, -25px, 0)
}
100%{transform:rotate(22deg) translate3d(0, 25px, 0)
}
}
.loadingLines{position:absolute;left:50%;top:50%
}
.loadingLines.show div{position:absolute;left:0;top:-30px;width:1px;height:60px;background:#16161f
}
.loadingLines.show .line1{margin-top:-6px;animation:piston .9s ease-in-out infinite
}
.loadingLines.show .line2{margin-top:6px;animation:piston_rev .9s ease-in-out infinite
}



